.selectTypeMode {
  width: 200px;
  position: relative;
  border-radius: 10px;
  border: 2px solid var(--COLOR_YELLOW_DEFAULT);
}

.selectTypeMode__arrow {
  transform: rotate(90deg);
  width: 16px;
  cursor: pointer;
  transition: all 0.1s linear;
}

.selectTypeMode__input {
  border-radius: 8px;
  justify-content: space-between;
}

.selectTypeMode__arrow.opened {
  transform: rotate(180deg);
}

.selectTypeMode__menu {
  width: 100%;
  position: absolute;
  top: 27px;
  left: 0;
  transition: all 0.1s linear;
  background-color: var(--COLOR_BLOCK);
  overflow: hidden;
  border-radius: 3px;
}

.selectTypeMode__item {
  display: flex;
  align-items: center;
  height: 27px;
  cursor: pointer;
  padding: 0 5px;
}

.selectTypeMode__item.selected {
  background: var(--COLOR_BLOCK_BORDER);
}

.selectTypeMode__menu.closed {
  max-height: 0px;
}

.selectTypeMode__menu.opened {
  max-height: 81px;
  border: 1px solid var(--COLOR_BLOCK_BORDER);
}

@media (max-width: 800px) {
  .selectTypeMode {
    width: 170px;
  }
}

@media (max-width: 480px) {
  .selectTypeMode {
    width: 150px;
  }
}
